.loader {
  width: 100%;
  height: 100%;
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  /* background-image: url(../../assets/huella_background.png);
  background-size: cover; */
  background-color: none;
  z-index: 9999;
}

.dogLoad {
 width: 80px;
 height: 80px;
 z-index: 9999;
 margin-bottom: 280px;
}
.dogLoad:before {
 content: '';
 display: block;
 position: absolute;
 width: 100%;
 height: 100%;
 background: rgba(118,118,118,0.233);
 border-radius: 50%;
  -webkit-transform: translateY(-30%) scale(1.5);
         transform: translateY(-30%) scale(1.5);
}
.dogLoad * {
 position: absolute;
}

.dogBody {
 top: -50%;
 -webkit-animation: dogBody 200ms ease-in-out infinite alternate;
         animation: dogBody 200ms ease-in-out infinite alternate;
}
.dogBody:before {
 content: '';
 position: absolute;
 bottom: 90%;
 right: 50%;
 width: 90%;
 height: 90%;
 border-top-left-radius: 100%;
 border-bottom-left-radius: 10%;
 border-top-right-radius: 10%;
 background: rgba(118,118,118, 0.233);
 -webkit-transform-origin: right bottom;
         transform-origin: right bottom;
 -webkit-animation: dogTail-blur 200ms 33.3333333333ms ease-in-out infinite alternate both;
         animation: dogTail-blur 200ms 33.3333333333ms ease-in-out infinite alternate both;
}
@-webkit-keyframes dogTail-blur {
 from {
   -webkit-transform: rotate(0);
           transform: rotate(0);
   opacity: 0;
 }
 50% {
   opacity: 1;
 }
 to {
   -webkit-transform: rotate(90deg);
           transform: rotate(90deg);
   opacity: 0;
 }
}
@keyframes dogTail-blur {
 from {
   -webkit-transform: rotate(0);
           transform: rotate(0);
   opacity: 0;
 }
 50% {
   opacity: 1;
 }
 to {
   -webkit-transform: rotate(90deg);
           transform: rotate(90deg);
   opacity: 0;
 }
}
@-webkit-keyframes dogBody {
 from {
   -webkit-transform: translateX(-10%);
           transform: translateX(-10%);
 }
 to {
   -webkit-transform: translateX(10%);
           transform: translateX(10%);
 }
}
@keyframes dogBody {
 from {
   -webkit-transform: translateX(-10%);
           transform: translateX(-10%);
 }
 to {
   -webkit-transform: translateX(10%);
           transform: translateX(10%);
 }
}

@-webkit-keyframes dogHead {
 from, to {
   -webkit-transform: rotate(45deg);
           transform: rotate(45deg);
 }
 33.3% {
   -webkit-transform: rotate(-45deg);
           transform: rotate(-45deg);
 }
 66.6% {
   -webkit-transform: rotate(0);
           transform: rotate(0);
 }
}
@keyframes dogHead {
 from, to {
   -webkit-transform: rotate(45deg);
           transform: rotate(45deg);
 }
 33.3% {
   -webkit-transform: rotate(-45deg);
           transform: rotate(-45deg);
 }
 66.6% {
   -webkit-transform: rotate(0);
           transform: rotate(0);
 }
}
.dogTorso {
 display: none;
 top: -20%;
 animation: dogTorso 200ms ease-in-out infinite alternate-reverse;
}
@-webkit-keyframes dogTorso {
 from {
   -webkit-transform: translateX(-5%);
           transform: translateX(-5%);
 }
 to {
   -webkit-transform: translateX(5%);
           transform: translateX(5%);
 }
}
@keyframes dogTorso {
 from {
   -webkit-transform: translateX(-5%);
           transform: translateX(-5%);
 }
 to {
   -webkit-transform: translateX(5%);
           transform: translateX(5%);
 }
}
.dogEyes {
 width: 60%;
 top: 46%;
 left: 20%;
 z-index: 1;
}
.dogEyes:before {
 content: '';
 display: block;
 height: 40px;
 width: 40px;
 border-radius: 40px;
 position: absolute;
 background: #F5832C;
 top: -10px;
 left: -10px;
 z-index: 0;
 box-shadow: inset -12px -8px 40px rgba(118,118,118,0.2);
 border: 4px solid #fcfcfc;
 border-left-width: 0;
 border-bottom-width: 0;
 border-top-width: 0;
 -webkit-transform: rotate(-45deg);
         transform: rotate(-45deg);

}

.dogEye {
 width: 10px;
 height: 10px;
 border-radius: 50%;
 background: #544258;
 z-index: 1;
 -webkit-animation: dogEye 1800ms infinite;
         animation: dogEye 1800ms infinite;
}
@-webkit-keyframes dogEye {
 from, to {
   -webkit-animation-timing-function: step-end;
           animation-timing-function: step-end;
   opacity: 1;
 }
 50%, 55% {
   -webkit-animation-timing-function: step-start;
           animation-timing-function: step-start;
   opacity: 0;
 }
}
@keyframes dogEye {
 from, to {
   -webkit-animation-timing-function: step-end;
           animation-timing-function: step-end;
   opacity: 1;
 }
 50%, 55% {
   -webkit-animation-timing-function: step-start;
           animation-timing-function: step-start;
   opacity: 0;
 }
}
.dogEye:first-child {
 left: 0;
}
.dogEye:last-child {
 right: 0;
}

.dogMuzzle {
 width: 60%;
 left: 20%;
 height: 50%;
 border-bottom-left-radius: 100%;
 border-bottom-right-radius: 100%;
 background: #f9f9f9;
 bottom: -15%;
 box-shadow: inset -12px -8px 40px rgba(255,255,255,0.233);
}







.dogMuzzle:before, .dogMuzzle:after {
 content: '';
 display: block;
 position: absolute;
}
.dogMuzzle:before {
 width: 6px;
 height: 20px;
 bottom: 0;
 left: calc(50% - 3px);
 background: rgba(84, 66, 88, 0.233);
}
.dogMuzzle:after {
 background: #544258;
 width: 20px;
 height: 15px;
 bottom: 12px;
 left: calc(50% - 10px);
 border-bottom-left-radius: 60% 60%;
 border-bottom-right-radius: 60% 60%;
 border-top-left-radius: 50% 40%;
 border-top-right-radius: 50% 40%;
}

.dogTongue {
 width: 40px;
 height: 100%;
 left: calc(50% - 20px);
 z-index: -1;
 -webkit-transform-origin: center top;
         transform-origin: center top;
 -webkit-animation: dogTongue 1800ms -50ms ease-in-out infinite;
         animation: dogTongue 1800ms -50ms ease-in-out infinite;
}
@-webkit-keyframes dogTongue {
 from, to {
   -webkit-transform: rotate(0);
           transform: rotate(0);
 }
 16.6666666667% {
   -webkit-transform: rotate(30deg);
           transform: rotate(30deg);
 }
 33.3333333333%, 66.6666666667% {
   -webkit-transform: rotate(0);
           transform: rotate(0);
 }
 50%, 83.3333333333% {
   -webkit-transform: rotate(-20deg);
           transform: rotate(-20deg);
 }
}
@keyframes dogTongue {
 from, to {
   -webkit-transform: rotate(0);
           transform: rotate(0);
 }
 16.6666666667% {
   -webkit-transform: rotate(30deg);
           transform: rotate(30deg);
 }
 33.3333333333%, 66.6666666667% {
   -webkit-transform: rotate(0);
           transform: rotate(0);
 }
 50%, 83.3333333333% {
   -webkit-transform: rotate(-20deg);
           transform: rotate(-20deg);
 }
}
.dogTongue:before {
 content: '';
 position: absolute;
 display: block;
 width: 100%;
 height: 100%;
 border-radius: 40px;
 background: #FD3163;
 -webkit-animation: dogTongue-inner 100ms ease-in-out infinite alternate;
         animation: dogTongue-inner 100ms ease-in-out infinite alternate;
}
@-webkit-keyframes dogTongue-inner {
 from {
   -webkit-transform: translateY(5%);
           transform: translateY(5%);
 }
 to {
   -webkit-transform: translateY(22%);
           transform: translateY(22%);
 }
}
@keyframes dogTongue-inner {
 from {
   -webkit-transform: translateY(5%);
           transform: translateY(5%);
 }
 to {
   -webkit-transform: translateY(22%);
           transform: translateY(22%);
 }
}
.dogEars {
 width: 40%;
 top: 16%;
 left: 30%;
 filter: drop-shadow(2px 4px 6px rgba(0,0,0,0.233));
 -webkit-animation: dogEars 1800ms 100ms ease infinite;
         animation: dogEars 1800ms 100ms ease infinite;
}
@-webkit-keyframes dogEars {
 42.3%, 71.6% {
   -webkit-transform: rotate(-5deg);
           transform: rotate(-5deg);
 }
 50.3%, 79.6% {
   -webkit-transform: rotate(5deg);
           transform: rotate(5deg);
 }
 5% {
   -webkit-transform: rotate(5deg);
           transform: rotate(5deg);
 }
 12% {
   -webkit-transform: rotate(-5%);
           transform: rotate(-5%);
 }
 from, 33.3%, 66%, to {
   -webkit-transform: rotate(0);
           transform: rotate(0);
 }
}
@keyframes dogEars {
 42.3%, 71.6% {
   -webkit-transform: rotate(-5deg);
           transform: rotate(-5deg);
 }
 50.3%, 79.6% {
   -webkit-transform: rotate(5deg);
           transform: rotate(5deg);
 }
 5% {
   -webkit-transform: rotate(5deg);
           transform: rotate(5deg);
 }
 12% {
   -webkit-transform: rotate(-5%);
           transform: rotate(-5%);
 }
 from, 33.3%, 66%, to {
   -webkit-transform: rotate(0);
           transform: rotate(0);
 }
}
.dogEar {
 bottom: -10px;
 height: 50px;
 width: 50px;
 background: #544258;
 -webkit-animation-duration: 400ms;
         animation-duration: 400ms;
 -webkit-animation-direction: alternate;
         animation-direction: alternate;
 -webkit-animation-timing-function: ease-in-out;
         animation-timing-function: ease-in-out;
 -webkit-animation-fill-mode: both;
         animation-fill-mode: both;
 -webkit-animation-iteration-count: infinite;
         animation-iteration-count: infinite;
}
.dogEar:first-child {
 border-bottom-left-radius: 95%;
 border-top-right-radius: 80%;
 right: 100%;
 box-shadow: inset -15px 8px 0 1px #cb8148;
 -webkit-transform-origin: right bottom;
         transform-origin: right bottom;
 -webkit-transform: rotate(10deg);
         transform: rotate(10deg);
}
.dogEar:last-child {
 border-top-left-radius: 95%;
 border-bottom-right-radius: 80%;
 left: 100%;
 box-shadow: inset 15px 8px 0 0 #cb8148;
 -webkit-transform-origin: left bottom;
         transform-origin: left bottom;
 -webkit-transform: rotate(-10deg);
         transform: rotate(-10deg);
}

.dogTail {
 width: 22px;
 height: 24.2px;
 background: #ffb141;
 bottom: 40%;
 border-radius: 11px;
 left: calc(50% - 11px);
 -webkit-transform-origin: center bottom;
         transform-origin: center bottom;
}
.dogTail .dogTail {
 -webkit-animation: dogTail-segment 200ms ease-in-out infinite alternate;
         animation: dogTail-segment 200ms ease-in-out infinite alternate;
}
@-webkit-keyframes dogTail-segment {
 from {
   -webkit-transform: rotate(-10deg);
           transform: rotate(-10deg);
 }
 to {
   -webkit-transform: rotate(10deg);
           transform: rotate(10deg);
 }
}
@keyframes dogTail-segment {
 from {
   -webkit-transform: rotate(-10deg);
           transform: rotate(-10deg);
 }
 to {
   -webkit-transform: rotate(10deg);
           transform: rotate(10deg);
 }
}
.dogBody > .dogTail {
 bottom: 90%;
 -webkit-animation: dogTail 200ms ease-in-out infinite alternate;
         animation: dogTail 200ms ease-in-out infinite alternate;
}
@-webkit-keyframes dogTail {
 from {
   -webkit-transform: rotate(-45deg);
           transform: rotate(-45deg);
 }
 to {
   -webkit-transform: rotate(45deg);
           transform: rotate(45deg);
 }
}
@keyframes dogTail {
 from {
   -webkit-transform: rotate(-45deg);
           transform: rotate(-45deg);
 }
 to {
   -webkit-transform: rotate(45deg);
           transform: rotate(45deg);
 }
}
.dogBody,
.dogTorso,
.dogHead {
 border-radius: 50%;
 background: #ffb141;
 position: absolute;
 height: 100%;
 width: 100%;
 box-shadow: inset -12px -8px 40px rgba(118, 118, 118, 0.233 );
 filter: drop-shadow(2px 4px 6px rgba(0,0,0,0.233)) brightness(100%);
}

.dogBody, .dogTorso {
/*  box-shadow: inset 0 -15px 0 0 #F5832C; */
box-shadow: inset -12px -8px 40px #F5832C;

background-image: url(data:image/png;base64,);


}

.dogHead {
 -webkit-animation: dogHead 1800ms cubic-bezier(0.11, 0.79, 0, 0.99) infinite;
         animation: dogHead 1800ms cubic-bezier(0.11, 0.79, 0, 0.99) infinite;
 background: #ffb141;
 background-image: url(data:image/png;base64,);

}


*, *:before, *:after {
 box-sizing: border-box;
 position: relative;
}


.dogMuzzle ,.dogMuzzle:before, .dogEyes:before, .dogEar:first-child, .dogEar:last-child, .dogTail {
background-image: url(data:image/png;base64,);

}
  